import Vue from 'vue'
import Vuetify from 'vuetify'
import App from './App.vue'

import 'vuetify/dist/vuetify.min.css'
import '@/stylus/main.styl'

Vue.config.productionTip = false

Vue.use(Vuetify, { theme: {
  primary: '#283593',
  secondary: '#FFB300',
}})

new Vue({
  render: h => h(App)
}).$mount('#app')
