<template>
<v-app>
  <v-toolbar app color='primary' dark>
    <v-toolbar-items>
      <v-tooltip bottom color='primary amber--text'>
        <v-btn flat large class='px-3' slot='activator' @click.native='onComeback'>
          <img src='@/assets/img/bg-asdl.png' />
        </v-btn>
        <strong>Go to the main page</strong>
      </v-tooltip>
    </v-toolbar-items>

    <v-spacer></v-spacer>

    <v-toolbar-items>
      <v-tooltip v-for='(x, i) in btns' :key='i' bottom color='primary amber--text'>
        <v-btn flat slot='activator' color='amber--text'>
          <v-icon small class='mr-2 amber--text'>fa-{{ x.icon }}</v-icon>
          <strong class='subheading amber--text'>{{ x.title }}</strong>
        </v-btn>
        <strong>{{ x.desc }}</strong>
      </v-tooltip>
    </v-toolbar-items>
  </v-toolbar>

  <v-content @mousemove.native='onMousemove'>
    <v-layout id='bgContainer'>
      <div id='background' :style='getBackgroundStyle' class='elevation-5'></div>

      <v-layout row justify-center align-center id='jumbotron'>
        <v-flex xs5 class='text-xs-center' style='min-width: 800px;'>
          <v-card hover class='px-5 py-3' style='background: rgba(40, 53, 147, 0.55); cursor: default'>
            <h1 class='jumbotronText display-4 amber--text mb-4'>EnergyWatch</h1>
            <h2 class='jumbotronText title amber--text'><i>for</i></h2>
            <h2 class='jumbotronText display-2 amber--text'>Georgia Institute of Technology</h2>
            <v-divider class='my-3'></v-divider>
            <h2 class='jumbotronText title amber--text my-2'>Powered by</h2>
            <h2 class='jumbotronText display-1 amber--text'>Aerospace Systems Design Laboratory</h2>
          </v-card>
        </v-flex>
      </v-layout>
    </v-layout>

    <v-layout justify-center class='pt-3 pb-5'>
      <v-flex xs12 md11 lg10 xl9>
        <v-layout row wrap class='pa-2'>
          <v-flex v-for='(x, i) in items' :key='i' xs12 sm6 md4 lg3 class='cardServiceWrapper pa-3' d-flex>
            <v-card class='cardService elevation-20'>
              <img :src='x.src' class='cardImage elevation-2'/>
              <div class='px-3 pt-1'>
                <h3 class="headline mb-0">{{ x.desc }}</h3>
              </div>

              <div class='cardButton text-xs-center'>
                <v-btn flat color='primary' @click.native='onClick(i)'>
                  <v-icon class='mr-2' small>fa-sign-in</v-icon>
                  <strong>Explore</strong>
                </v-btn>
              </div>
            </v-card>
          </v-flex>
        </v-layout>
      </v-flex>
    </v-layout>
  </v-content>

  <v-footer color='primary' class='pa-4' dark>
    <v-layout justigfy-center align-center class='text-xs-center'>
      <v-flex>
        <span class='title amber--text'>&copy;2018 Georgia Institute of Technology</span>
      </v-flex>
    </v-layout>
  </v-footer>

  <v-snackbar color='primary amber--text' v-model='snackbar' bottom left :timeout='timeout'>
    <strong>{{ snackText }}</strong>
  </v-snackbar>
</v-app>
</template>

<script>
export default {
  data: () => ({
    x: 0,
    y: 0,
    center: { x: window.innerWidth / 2, y: window.innerHeight / 2 },
    btns: [
      { icon: 'users', title: 'Intro', desc: 'Introduction of our project' },
      { icon: 'table', title: 'The Loop', desc: 'Table for reported issues' },
      { icon: 'thumbs-up', title: 'Success Stories', desc: 'Hall of conspicuous improvements' },
    ],
    items: [{
      desc: 'Plant Efficiency Monitoring',
      src : require('@/assets/img/img_feature0.jpg'),
      link: 'http://fin02:8080/proj/demo/builder',
    }, {
      desc: 'Chilled Water Network Analysis',
      src : `${ require('@/assets/img/img_feature1.jpg') }`,
      link: 'http://energywatch.fac.gatech.edu:5000',
    }, {
      desc: 'Buliding Fleet Monitoring',
      src: `${ require('@/assets/img/img_feature2.jpg') }`,
      link: 'http://energywatch.fac.gatech.edu',
    }, {
      desc: 'Temporal Energy Analysis',
      src : `${ require('@/assets/img/img_feature3.jpg') }`,
      link: 'http://energywatch.fac.gatech.edu:3000/heatmap',
    }, {
      desc: 'Energy Pattern Analysis',
      src : `${ require('@/assets/img/img_feature4.jpg') }`,
      link: 'http://energywatch.fac.gatech.edu:3000/fingerprint'
    }, {
      desc: 'ION Data Grabber',
      src : `${ require('@/assets/img/img_feature5.jpg') }`,
      link: 'http://energywatch.fac.gatech.edu:9000',
    }, {
      desc: 'Utilities Tracker',
      src : `${ require('@/assets/img/img_feature6.jpg') }`,
      link: 'http://energywatch.fac.gatech.edu:3000/webtools/billtracker',
    }, {
      desc: ' Rollcalls for ION Data points',
      src : `${ require('@/assets/img/img_feature7.jpg') }`,
      link: 'http://energywatch.fac.gatech.edu:3000/webtools/rollcallall',
    }],
    snackbar: false,
    snackText: 'Sorry, this app is under maintenance.',
    timeout: 3000,
  }),
  computed: {
    getBackgroundStyle() {
      return {
        'background-position': `calc(50% + ${ this.x }px) calc(50% + ${ this.y }px) !important`
      }
    }
  },
  methods: {
    onMousemove($e) {
      this.x = (this.center.x - $e.clientX) / 5
      this.y = (this.center.y - $e.clientY) / 5
    },
    onClick(i) {
      if(i === 1) return this.snackbar = true
      window.open(this.items[i].link)
    },
    onComeback() {
      // this.$router.push('/')
    }
  },
}
</script>


<style lang='stylus'>
#bgContainer
  position: relative
  height: 45vh
#background
  position: absolute
  width: 100%
  height: 100%
  top: 0
  left: 0
  background-image: url(./assets/img/bg.jpg)
  background-size: 130%
  background-repeat: no-repeat

#jumbotron
  position: absolute
  width: 100%
  height: 100%
  top: 0
  left: 0
  .jumbotronText
    text-shadow: 0 0 10px rgba(255, 230, 0, 0.6)
    font-weight: bold

.cardServiceWrapper
  transform: translateY(0) scale(1)
  transition: all 0.25s linear
  &:hover
    transform: translateY(-4px) scale(1.01)

.cardService
  position: relative
  height: 350px
  .cardImage
    width: 100%
    height: 220px
  .cardButton
    position: absolute
    width: 100%
    left: 0
    bottom: 0
</style>
